import flatpickr from 'flatpickr';
import { Korean } from 'flatpickr/dist/l10n/ko';
import { datepicker } from '../elements';

const orderPicker = datepicker.order;
const dawnPicker = datepicker.dawn;

if (orderPicker !== null) {
    flatpickr(orderPicker, {
        locale: Korean,
        enableTime: true,
        dateFormat: 'Y-m-d H:i',
        disableMobile: 'true',
        minDate: 'today',
        wrap: true
    });
}

if (dawnPicker !== null) {
    flatpickr(dawnPicker, {
        locale: Korean,
        // enableTime: true,
        dateFormat: 'Y.m.d',
        disableMobile: 'true'
        // minDate: 'today',
        // wrap: true
    });
}
