import { pages, early } from '../elements';

if (pages.early !== null) {
    const directCheck = early.direct.check;
    const directInput = early.direct.input.children[0];
    const directSpan = early.direct.span;
    const inDirectCheck = early.indirect.check;
    const inDirectSelect = early.indirect.select;

    // directInput.disabled = true;
    inDirectSelect.disabled = true;

    const swichChoice = e => {
        const checkBox = e.currentTarget.children[0];
        const directCheckBox = directCheck.children[0];
        const inDirectCheckBox = inDirectCheck.children[0];

        console.log(e.currentTarget);

        if (e.currentTarget == directCheck) {
            if (checkBox.checked) {
                //   true 
                //  
                checkBox.checked = true;
                directInput.disabled = false;
                directInput.style.opacity = 1;
                directSpan.style.opacity = 1;

                //  
                inDirectCheckBox.checked = false;
                inDirectSelect.disabled = true;
            } else {
                checkBox.checked = false;
                directInput.value = '';
                directInput.disabled = true;
                directInput.style.opacity = 0.3;
                directSpan.style.opacity = 0.3;

                //  
                inDirectCheckBox.checked = true;
                inDirectSelect.disabled = false;
            }
        }

        if (e.currentTarget == inDirectCheck) {
            if (checkBox.checked) {
                //   true 
                //  
                directInput.value = '';
                directCheckBox.checked = false;
                directInput.disabled = true;
                directInput.style.opacity = 0.3;
                directSpan.style.opacity = 0.3;

                //  
                checkBox.checked = true;
                inDirectSelect.disabled = false;
            } else {
                directCheckBox.checked = true;
                directInput.disabled = false;
                directInput.style.opacity = 1;
                directSpan.style.opacity = 1;

                //  
                checkBox.checked = false;
                inDirectSelect.disabled = true;
            }
        }
    };

    directCheck.addEventListener('click', swichChoice);
    inDirectCheck.addEventListener('click', swichChoice);
}
