import { navigation } from '../adminElements';

const mobileIcon = navigation.mobile.icon;
const mobileCover = navigation.mobile.cover;
const mobileContainer = navigation.mobile.container;
const mobileClose = navigation.mobile.close;

if (navigation.self !== null) {
    mobileIcon.addEventListener('click', () => {
        mobileCover.classList.add('active');
        mobileContainer.classList.add('active');
    });

    mobileCover.addEventListener('click', () => {
        mobileCover.classList.remove('active');
        mobileContainer.classList.remove('active');
    });

    mobileClose.addEventListener('click', () => {
        mobileCover.classList.remove('active');
        mobileContainer.classList.remove('active');
    });
}
